

.headerContainer {
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    height:8vh;
    align-items: center;
    background-color:#0a192f;
}

.navContainer {
    display: flex;
    flex-direction: row;
    width: 50vh;
    margin-right: 10vh;
    justify-content: space-around;
    align-items: center;
    list-style-type: none;
    color: white;
}

.navContainer li {
    display: inline;
    padding-left: 10px;
    padding-right: 10px;
}

.navContainer li:hover {
    color: #64FFDA;
    cursor: pointer;
}

.logoContainer {
    margin-left: 10vh;
    color: #64FFDA;
}

.index {
    color: #64FFDA;
}

/* smaller screens */

@media screen and (max-width: 800px) {
    .navContainer {
        display: none;
        border: 2px solid black;  
    }

    .headerContainer {
        justify-content: center;
        align-items: center;
    }

    .logoContainer {
        margin: 0;
        align-items: center;
    }
}
* {
    margin: 0;
    padding: 0;
    background-color:#0a192f;
    font-family: "Calibre","Inter","San Francisco","SF Pro Text",-apple-system,system-ui,sans-serif;
}

.homeContainer {
    height: 40vh;
    color:white;
    display:flex;
    justify-content:center;
    border: 2px solid green;
    align-items:center;
    width: 100%;
}
.introContainer {
    margin: 15vh 15vh 10vh 10vh;
    /* border: 2px solid white; */
    /* min-width: 480px; */
}

.myName {
    font-size: clamp(30px, 6vw, 80px);
    font-weight: 600;
    line-height: 1.1;
    margin-top: 1vh;
    color: #CCD6F6;
}

.introduction {
    color: #64FFDA;
    font-size: clamp(14px,5vw,16px);
}

.introductionText {
    color: #8892B0; 
    line-height: 1.2;
    margin-top: 1vh;
}

.trybe {
    color: #036b52;
    font-weight: bold;
}

/* smaller screens */

body {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 800px) {
    .homeContainer {
        /* background-color: black; */
        justify-content: center;
        align-items: center;
    }
    .introContainer {
        margin: 3vh;
        margin-left: 6vh;
    }
}

@media screen and (max-width: 375px) {
    html{
        width: 375px;
        overflow: auto;
    }
}

